import { Injectable } from '@angular/core';
import { UploadServerService, UploadFile, UploadInput, UploadStatus } from '@farris/extend-file-upload';
import { Observable, forkJoin } from 'rxjs';
import { UploadService, GspFormUploadListEntity, GspFormRemoveListEntity, GspFormDocInfo, GspFormUploadEntity, FileInfo, OperatingModes, GspDocMetaProperty } from '@gsp-svc/formdoc-upload';
import { FFileUploadAdaptUnifileConfigService, FFileUploadAdaptUnifileConfig } from './ffileupload-adapt-unifile.config';

@Injectable({
  providedIn: 'root'
})
export class FfileuploadAdaptUnifileService extends UploadServerService {
  extendData;// 暂时用于简单合并
  bufferSize = 1024*1024;// 1024 * 1024
  private static previous = 0;
  private uploadedChunk = {};
  private fileTotalChunk = {};

  constructor(private uploadSer: UploadService, private configSer: FFileUploadAdaptUnifileConfigService) {
    super();
    this.extendData = this.configSer.getConfig();
  }

  private uuid() {
    let  S4 = function () {
      return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
    };
    let nGuid = S4() + S4() +  S4() +  S4() +  S4() +  S4() + S4() + S4();
    return nGuid;
  }
  /**
   * 删除文件
   * @param file 
   * @param event 
   */
  remove(files: UploadFile[], event: UploadInput, extendService: FFileUploadAdaptUnifileConfig): Observable<any> {
    return this.innerRemoveList(files, event, extendService);
  }

  private innerRemoveList(files: UploadFile[], event: UploadInput, extendService = null): Observable<any> {
    return new Observable(observer => {
      let removeFileInfo = new GspFormRemoveListEntity;
      let metadataIdList = [];
      files.forEach(fileItem => {
        if (fileItem['response']) {
          metadataIdList.push(fileItem['response']['metadataId']);
        }
      });

      removeFileInfo.mode = this.getFinallyConfig('mode', extendService);
      let rootId = this.getFinallyConfig('rootId', extendService);
      removeFileInfo.metadataIdList = [].concat(metadataIdList);

      this.uploadSer.removeList(metadataIdList, rootId).subscribe(result => {
        // 没有需要整合的服务器端返回数据
        observer.next({ type: 'removed', files: files });
      }, error => {
        observer.error(error);
        observer.complete();
      }, () => {
        observer.complete();
      });
    });
  }
  /**
   * 上传文件
   * @param file 
   * @param event 
   */
  upload(files: UploadFile[], event: UploadInput, extendService: FFileUploadAdaptUnifileConfig): Observable<any> {
    return this.innerUploadList(files, event, extendService);
  }
  /**
   * 内部上传方式
   * @param files 
   * @param event 
   * @param extendService 
   */
  private innerUploadList(files: UploadFile[], event: UploadInput, extendService: FFileUploadAdaptUnifileConfig): Observable<any> {
    return new Observable(observer => {

      let uploadInfo = new GspFormUploadListEntity;
      uploadInfo.formId = this.getFinallyConfig('formId', extendService);
      uploadInfo.mode = this.getFinallyConfig('mode', extendService);

      let rootId = this.getFinallyConfig('rootId', extendService);
      uploadInfo.docInfoList = [];
      let readerObserable: Observable<any>[] = [];
      files.forEach((fileItem: UploadFile) => {
        let sub = new Observable(obser => {
          let reader = new FileReader();
          // 暂不处理异常
          reader.readAsBinaryString(fileItem.nativeFile);
          reader.onload = (e) => {
            let info = { 'fileName': '', 'fileContent': '' } as any;
            info['fileName'] = fileItem.name;
            info['fileContent'] = btoa(reader.result.toString());
            // 如果data里有数据，并且属性是配置的扩展属性
            if (event.hasOwnProperty('data') && event['data'] && event['data'].hasOwnProperty('extProperty')) {
              // 
              info['extProperty'] = event['data']['extProperty'];
            }
            uploadInfo.docInfoList.push(info);
            obser.next();
            obser.complete();
          }
        });
        readerObserable.push(sub);
      });

      forkJoin(readerObserable).subscribe(datas => {
        //     metadataId: string;
        //  fileName: string;
        this.uploadSer.uploadList(uploadInfo, rootId).subscribe(result => {
          if (result.error) {
            // 此接口一旦出现问题，所有附件都上传不通过
            observer.error(this.errorInfoFormat(result.error, files));
            observer.complete();
            return;
          }
          // 合并返回结果到此处
          result.forEach(item => {
            var findIndex = this.findFileIndexByFileName(files, item.fileName);
            if (findIndex > -1) {
              files[findIndex]['response'] = item;
              files[findIndex]['progress']['status'] = UploadStatus.Done;
            }
          });

          observer.next({ type: 'done', files: files });

        }, error => {
          observer.error(this.errorInfoFormat(error, files));
          observer.complete();
        }, () => {
          observer.complete();
        });
      });
    });
  };
  /**
   * 分片加载
   * @param file 
   * @param event 
   * @param extendService 
   */
  multipartUpload(file: UploadFile, event: UploadInput, extendService: FFileUploadAdaptUnifileConfig): Observable<any> {

    return new Observable(observer => {

     // let fileInfo: FileInfo;
      let uuid = this.uuid();
      // let uuid = UUID.UUID();
      //reader.readAsBinaryString(selectedFile);
      //reader.onload = (e) => {
      let fileName = file.name;
      // fileInfo = {
      //   id: uuid,
      //   name: fileName,
      //   type: file.type,
      //   source: "",
      //   size: this.formatFileSize(file.size),
      //   fileSize: file.size,
      //   picListDisplayName: this.getMultipartDisplayName(fileName),
      //   queueListDisplayName: fileName,
      //   //content: reader.result.toString(),
      //   hasUploaded: false,
      //   mouseOn: false,
      //   selectd: false,
      //   isUploading: true,
      //   uploadResult: false,
      //   uploadProcess: 0,
      //   errorMessage: "",
      //   extensionDropListId: 0,
      //   extensionName: ""
      // }

      let chunkTotal: number = Math.ceil(file.size / this.bufferSize);
      // 标记当前附件总的分片数
      this.fileTotalChunk[uuid] = chunkTotal;
      let chunkIndex: number = 0;
      // 标记当前附件成功的分片数
      this.uploadedChunk[uuid] = 0;
      while (chunkIndex < chunkTotal) {
        let uploadInfo = new GspFormUploadEntity;
        uploadInfo.mode = OperatingModes.Temp;
        uploadInfo.formId = this.getFinallyConfig('formId', extendService);
        uploadInfo.rootId = this.getFinallyConfig('rootId', extendService);

        let docInfo: GspFormDocInfo = new GspFormDocInfo;
        docInfo.fileName = fileName;
        docInfo.metadataId = uuid;
        docInfo.total = chunkTotal;
        // 如果data里有数据，并且属性是配置的扩展属性
        if (event.hasOwnProperty('data') && event['data'] && event['data'].hasOwnProperty('extProperty')) {
          docInfo['extProperty'] = event['data']['extProperty'] as GspDocMetaProperty;
        }
        // 下一个文件大小
        let nextSize = Math.min((chunkIndex + 1) * this.bufferSize, file.size);
        // File类型
        let fileData = file['nativeFile'].slice(chunkIndex * this.bufferSize, nextSize);
        // 读取文件
        let reader = new FileReader();
        reader.readAsBinaryString(fileData);
        let innerIndex = chunkIndex;
        reader.onload = () => {
          // 附件上传
          docInfo.fileContent = btoa(reader.result.toString());
          docInfo.index = innerIndex;

          uploadInfo.docInfo = docInfo;
          let info = uploadInfo;

          this.uploadSer.uploadFile(info).subscribe(result => {
            if (result && result.error) {
              // 分片上传有返回结果就是报错了
              observer.error(this.errorInfoFormat(result.error, [file]));
              observer.complete();
              return;
            } else {
              this.uploadedChunk[uuid]++;
              if (this.uploadedChunk[docInfo.metadataId] == this.fileTotalChunk[docInfo.metadataId]) {
                // 判断是所有分片都上传完成
                file.progress = {
                  status: UploadStatus.Done,
                  data: {
                    percentage: 100
                  }
                };
                file.response=docInfo;
                delete this.uploadedChunk[uuid];
                delete this.fileTotalChunk[uuid];
                observer.next({ type: 'done', files: [file] });
                observer.complete();

              } else {                
              // 分片上传正常
                const percentage = Number.parseInt((this.uploadedChunk[uuid] / this.fileTotalChunk[uuid] * 100).toFixed(0));
                // 判断未上传完成
                file.progress = {
                  status: UploadStatus.Uploading,
                  data: {
                    percentage: percentage
                  }
                };
                observer.next({ type: 'uploading', files: [file] });
              }
            }
          }, error => {
            delete this.uploadedChunk[uuid];
            delete this.fileTotalChunk[uuid];
            observer.error(this.errorInfoFormat(error, [file]));
            observer.complete();
          });
        }
        chunkIndex = chunkIndex + 1;
      }
    });
  }
  /**
   * 获取分片的附件大小
   * @param size 
   */
  private formatFileSize(size: number) {
    if (size < 102400)
      return (size / 1024).toFixed(1) + "K";
    else if (size < 1024 * 1024)
      return (size / 1024).toFixed(0) + "K";
    else if (size < 100 * 1024 * 1024)
      return (size / 1024 / 1024).toFixed(1) + "M";
    else if (size < 1024 * 1024 * 1024)
      return (size / 1024 / 1024).toFixed(0) + "M";
    else
      return (size / 1024 / 1024 / 1024).toFixed(1) + "G";
  }
  /**
   * 获取分片的 附件名称
   * @param fileName 
   */
  private getMultipartDisplayName(fileName: string) {
    if (fileName.length <= 10)
      return fileName;
    else {
      let headContent = fileName.substring(0, 2);
      let tailContent = fileName.substring(fileName.lastIndexOf('.') - 2);
      return headContent + "…" + tailContent;
    }
  }
  private errorInfoFormat(error, files) {
    // 
    let errorFiles = files.map(item => {
      return { id: item.id, name: item.name };
    });
    if (error) {
      return Object.assign(error, { files: errorFiles }, { message: error['Message'] || error['extensionMessage'] || '上传失败', type: 'error' });
    } else {
      return Object.assign({ files: errorFiles }, { message: '上传失败', type: 'error' });
    }
  }
  /**
   * 返回最终属性值
   * @param key 
   * @param configValue 
   * @param extendValue 
   */
  private getFinallyConfig(key, extendValue) {
    if (extendValue && extendValue.hasOwnProperty(key)) {
      return extendValue[key];
    }
    return this.extendData[key];
  }

  private findFileIndexByFileName(files: UploadFile[], fileName) {
    // 按照肯定能找到文件来处理
    let findIndex = files.findIndex(file => file.name == fileName);
    return findIndex;
  }

}

